<template>
  <div class="home">
   <div class="jp-content">
     <!--轮播图-->
     <div class="swiper-container">
       <div class="swiper-wrapper">
         <div class="swiper-slide">
           <router-link to="/">
             <img width="100%" src="/static/image/banner.png" alt="广告图">
           </router-link>
         </div>
         <div class="swiper-slide">
           <router-link to="/">
             <img  width="100%" src="/static/image/banner.png" alt="广告图">
           </router-link>
         </div>
       </div>
       <!-- 分页器 -->
       <div class="swiper-pagination"></div>
     </div>

     <div class="list-title">
       <em class="border-2px redBg"></em><span class="title">任务精选</span>
     </div>

     <ul class="list-item">
       <li class="item" v-for="(item,$index) in someData">
            <router-link :to="{ name: 'Details', params: { userId: item.id }}">
           <div class="item-left">
             <img :src="item.c_img" :alt="item.c_name">
             <span class="item-name" v-text="item.c_name"> </span>
             <span class="text-right"><i class="jp-ico_residual_task"></i>剩余佣金 <span>840</span> 元</span>
           </div>
           <div class="item-right">

             <div class="item-div">
               <span>投资本金 <span>{{item.capital}}元</span></span>
               <span>投资利息 <span><strong>{{item.interest_rate}}</strong>元</span></span>
             </div>
             <div class="item-div">
               <span>投资期限 <span>{{item.duration}}天</span></span>
               <span>剩余任务 <span>{{item.last_num}}个</span></span>
             </div>
             <div class="price">
               <div class="new-price">
                 <span class="text">任务返利</span>
                 <span class="prices">￥<em>{{item.money}}</em></span>
               </div>

             </div>
           </div>
          </router-link>
       </li>

     </ul>
       <mu-infinite-scroll :scroller="scroller" :loading="loading" @load="loadMore"/>
     </div>

    <!--底部导航-->
    <nav-gation :clicked="true" :clicked2="true"></nav-gation>
  </div>
</template>

<script type="text/ecmascript-6">
  import '../../../static/js/swiper-3.4.2.min'
  import Navigation from '../navigation/navigation'

  export default {
    //mounted 生命周期钩子
    data() {
        return {
          someData:[],
          nexturl:'',
          loading: false,
          num: 10,
          scroller: null
        }
    },
    mounted() {
      this.scroller = this.$el
      //实例化轮埠图
      var mySwiper = new Swiper ('.swiper-container', {
        loop: true,
        autoplay: 3000,
        // 如果需要分页器
        pagination: '.swiper-pagination',
      })
      this.$nextTick(function () {
          this.$http.get('/api/home/indexs' ).then(response=> {
            const rel = response.body
            this.someData = rel.info
          })
      })
    },
    methods: {
      loadMore () {

        this.loading = true
        setTimeout(() => {
          this.$http.get('/api/Home/indexs/p/2.html').then(response=> {
            console.log(response)
          })
          this.num += 10
          this.loading = false
        }, 2000)
      }
    },
    components: {
        'nav-gation' : Navigation,

    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '../../style/css/swiper-3.4.2.min.css';

  .home
    height 100%;
    .jp-content
      .swiper-pagination-bullet
        width 0.2rem
        height 0.2rem
        margin 0 0.12rem
      .demo-infinite-container
        height: 90%;
</style>



