<template>
  <div class="balance">
    <div class="mui-content">

      <div style="padding-top: 0.266rem;">
        <div id="segmentedControl" class="mui-segmented-control mui-segmented-control-inverted mui-segmented-control-primary">
          <a class="mui-control-item  mui-active" href="javascript:;" value="">
            全部
          </a>
          <a class="mui-control-item " href="javascript:;" value="1">
            任务返利
          </a>
          <a class="mui-control-item  " href="javascript:;" value="2">
            提现明细
          </a>
        </div>
      </div>
      <div class="h20 bgf5"></div>
      <div class="mui-scroll-wrapper mui-control-content mui-active" id="refreshContainer">
        <div class="mui-scroll">
          <ul class="mui-table-view show">
            <li class="mui-table-view-cell">
              <div class="title">
                <span class="titles">任务返现</span>
                <small class="time">2017-03-17 16:40:21</small>
              </div>

              <span class="mui-badge  ">+120.00</span>
            </li>
            <li class="mui-table-view-cell">
              <div class="title">
                <span class="titles">任务返现</span>
                <small class="time">2017-03-17 16:40:21</small>
              </div>

              <span class="mui-badge  ">+120.00</span>
            </li>
            <li class="mui-table-view-cell">
              <div class="title">
                <span class="titles">任务返现</span>
                <small class="time">2017-03-17 16:40:21</small>
              </div>

              <span class="mui-badge green ">-120.00</span>
            </li>
            <input type="hidden" class="nexturl" value="aa">
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  //mui滚动条下执行a标签跳转
  mui('body').on('tap','a',function () {
    document.location.href=this.href
  })

  mui.init({
    pullRefresh : {
      container:"#refreshContainer",//待刷新区域标识，querySelector能定位的css选择器均可，比如：id、.class等
      up : {
        height:50,// 可选.默认50.触发上拉加载拖动距离
        auto:false,// 可选,默认false.自动上拉加载一次
        callback : function() {
          var self = this; // 这里的this == mui('#refreshContainer').pullRefresh()
          // 加载更多的内容
          loadMore(this);
        } //必选，刷新函数，根据具体业务来编写，比如通过ajax从服务器获取新数据；
      }
    }
  });

  //
  var loadMore = function(pullRefresh) {
    var url = $(".nexturl:last").val();
    if(url=='aa'){
      pullRefresh.endPullupToRefresh(true);
      return false;
    }
    mui.ajax(url,{
      data:{},
      dataType:'json',//服务器返回json格式数据
      type:'get',//HTTP请求类型
      success:function(data){
        //获得服务器响应
        $(".show").append(data);
      }
    });

    // 如果没有更多数据了，则关闭上拉加载
    //pullRefresh.endPullupToRefresh(true);
    // 如果有更多数据，则继续
    pullRefresh.endPullupToRefresh(false);
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .balance
    .mui-control-content.mui-active
      display: block;
      position: absolute;
      top: 1.36rem;
      bottom: 0;
      left: 0;
      right: 0;
    .mui-badge
      background-color: inherit;
      font-size 0.48rem
      color: #fb5a5a;
      &.green
        color #01bf5f
    .title .titles,.title .time
      display: block;font-size:0.4rem;color: #333;
    .title .titles
      line-height: 0.55rem
    .title .time
      color: #666;font-size: 0.34rem;



</style>



